<template>
  <div class="automata">
    <h1>Cellular Automata</h1>
    <section>
      <p>
        Stanisław Marcin Ulam was a polish nuclear physicist who discovered the Monte Carlo method and
        cellular automata, among other achievements. The idea of a cellular automaton came up from
        thinking about the growth of crystals as self-replicating robots. A cellular automaton system
        consists of a grid of cells that have certain mutable state. The cellular automaton showed here
        was mentioned by Stephen Wolfram in his book <i>A new Kind of Science</i>.
      </p>
      <p>
        Starting with one "live" cell in a row, a ruleset determines the next generation in the next row.
        There are 256 rulesets because a row has 8 cells (256 = 2 to the eigth power). One of the most
        interesting rulesets is the number 90 which originates the "Sierpiński triangle".
      </p>
      <p>
        To originate the different cellular automata, write the number of the ruleset in the input
        space (a number from 0 to 255) and click the button:
      </p>
    </section>
    <input v-on:keyup.enter="stepGenerate" type="text" v-model.number="rule" placeholder="write number between 1 and 255">
    <button @click="stepGenerate">Click</button>
    <p v-bind:style="{color: 'red'}" v-if="rule > 255 || rule < 0">Input is out of range</p>
    <div class="squares">
      <Square 
      class="cuadro"
      v-bind:class="{ gris: elem === 1 }"
      v-for="(elem, index) in list"
      v-bind:elem="elem" v-bind:key="index"/>
    </div>
  </div>
</template>

<script>
const WIDTH = 31;
const r = ['111', '110', '101', '100', '011', '010', '001', '000'];
const createList = (num) => {
  let seq = [];
  for (let k = 0; k < num; k++) {
    seq.push(0);
  }
  seq[15] = 1;
  return seq;
}
import Square from './Square.vue';

export default {
  name: 'CellularAutomata',
  components: {
    Square
  },
  methods: {
    getNeighborsIndices: function(indx) {
      let left = indx === 0 ? 30 : (indx - 1) % WIDTH + indx - (indx % WIDTH);
      let right = (indx + 1) % WIDTH + indx - (indx % WIDTH);
      return [left, indx, right];
    },
    applyRuleset: function(arr) {
      return parseInt(this.ruleset[r.indexOf(arr)]);
    },
    createRuleset: function(ruleNumber) {
      let bin = ruleNumber.toString(2);
      let pad = 8 - bin.length;
      for (let j = 0; j < pad; j++) {
        bin = '0' + bin;
      }
      return bin;
    },
    stepGenerate: function() {
      if (this.rule > 255 || this.rule < 0) return;
      this.ruleset = this.createRuleset(this.rule);
      let copy = createList(496);
      //let counter = 0;
      //let linea = [];
      for (let i = 0; i < 15; i++) {
        for (let j = i * WIDTH; j < i* WIDTH + WIDTH; j++) {
          let valores = this.getNeighborsIndices(j).map(x => this.list[x]).join('');
          let newValue = this.applyRuleset(valores);
          copy[j + WIDTH] = newValue;
          //linea.push(newValue);
          //this.list[j + WIDTH] = newValue;
          //this.getNeighborsIndices(j);
          //console.log(this.applyRuleset(this.getNeighborsIndices(j)));
          //valores = valores.join('');
          //console.log(this.ruleset);
          //console.log(valores);
          //console.log(r.indexOf(valores));
          //console.log(this.applyRuleset(valores));
        }
        //counter++;
        //console.log(counter);
        //if (counter > 10) break;
        this.list = Object.assign({}, copy);
      }
      //console.log(copy);
      //this.list = copy;
    }
  },
  data: function() {
    return {
      rule: 30,
      list: createList(496),
      ruleset: null
      //ruleset: this.createRuleset(250)
    }
  }
}
</script>

<style scoped>
h1 {
  text-align: center;
}
.squares {
  display: grid;
  grid-template-columns: repeat(31, 1fr);
  grid-template-rows: repeat(16, 15px);
}
.automata {
  margin: 7em auto;
  width: 40%;
}
.gris {
  background: rgb(7, 40, 189);
}
