<template>
  <div class="container">
    <h1>Langton's Ant</h1>
    <section>
      <p>
        Langton's ant is a cellular automaton where the color of the cell changes depending on 
        the ant's movement. After more than 10'000 steps of chaotic movement, the ant describes
        a repetitive pattern. According to Wikipedia:
      </p>
      <blockquote>
        Langton's ant is a two-dimensional universal Turing machine with a very simple set of rules
        but complex emergent behavior. It was invented by Chris Langton in 1986 and runs on a square
        lattice of black and white cells. The universality of Langton's ant was proven in 2000.
        The idea has been generalized in several different ways, such as turmites which add more
        colors and more states. 
      </blockquote>
    </section>
    <div class="buttons">
      <button @click="startAnimation">Start Animation</button>
      <button @click="stopAnimation">Reset</button>
    </div>
    <div class="langton-ant-container">
      <Square :class="{ant: indx === antIndex}" v-for="(item, indx) in squareList" v-bind:key="indx" v-bind:elem="item"/>
    </div>
  </div>
</template>

<script>
import Square from './Square.vue';

const WIDTH = 79;
const MIDPOINT = Math.round(WIDTH * WIDTH / 2) - 1;

const numGenerator = (limit) => {
  let seq = [];
  for (let k = 0; k < limit; k++) {
    seq.push(0);
  }
  seq[MIDPOINT] = 1;
  return seq;
}

const SHIFTS = {0: 1, 90: -WIDTH, 180: -1, 270: WIDTH};
// const getIndex = (dir, index, turn) => {
//   let shifts = {0: 1, 90: WIDTH, 180: -1, 270: -WIDTH};
//   let newDir = dir + turn;
//   if (newDir === -90) newDir = 270;
//   return shifts[newDir] + index;
// }

export default {
  name: 'LangtonAnt',
  components: {
    Square
  },
  methods: {
    startAnimation: function() {
      if (this.animar === null || this.animar === 0) {
        this.animar = setInterval(this.moveAnt2, 5);
      }
    },
    stopAnimation: function() {
      this.squareList = numGenerator(WIDTH * WIDTH);
      this.animar = null;
      this.antIndex = MIDPOINT;
      this.counter = 0;
      this.antDirection = 90;
      // clearInterval(this.animar);
      // this.animar = 0;
    },
    getIndex: function(turn) {
      let newDir = this.antDirection + turn;
      if (newDir === -90) newDir = 270;
      if (newDir === 360) newDir = 0;
      this.antIndex = SHIFTS[newDir] + this.antIndex;
      this.antDirection = newDir;
    },
    moveAnt: function() {
      this.counter++;
      if (this.squareList[this.antIndex] === 1) {
        this.squareList[this.antIndex] = 0;
        this.getIndex(90);
      }
      else {
        this.squareList[this.antIndex] = 1;
        this.getIndex(-90);
      }
    },
    moveAnt2: function() {
      this.counter++;
      if (this.counter > 110) {
        clearInterval(this.animar);
        this.animar = 0;
      }
      for (let k = 0; k < 100; k++) {
        if (this.squareList[this.antIndex] === 1) {
          this.squareList[this.antIndex] = 0;
          this.getIndex(90);
        }
        else {
          this.squareList[this.antIndex] = 1;
          this.getIndex(-90);
        }
      }
    }
  },
  data: function() {
    return {
      squareList: numGenerator(WIDTH * WIDTH),
      antIndex: MIDPOINT,
      antDirection: 90,
      animar: null,
      counter: 0
    }
  }
}
</script>

<style scoped>
h1 {
  text-align: center;
}
.container {
  margin: auto;
  /* text-align: center; */
  width: 50%;
}
.langton-ant-container {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(79, 5px);
  grid-template-rows: repeat(79, 5px);
  margin: auto;
}
.buttons {
  text-align: center;
}
.ant {
  border: 2px solid red;
}
</style>
