<template>
  <div class="container">
    <h1>Coronavirus Nucleotides</h1>
    <p>
      
    </p>
    <p>
      The coronavirus genome is made up of nucleotides represented by four basic letters: A (adenine), C (cytosine),
      G (guanine), and T (thymine). Here we use a grid of cells to represent part of a string of the 
      coronavirus taking into account the next convention:
    </p>
    <div class="convention">
      <DnaBox v-for="(item, index) in bases" v-bind:key="index">
        {{item}}
      </DnaBox>
      <DnaBox v-for="(item, index) in bases" v-bind:key="index" v-bind:elem="item"/>
    </div>
    <p>
      What could be the use of a grahical representation of a genome? Unlike the representation of the
      digits of pi, a genome carries information to code proteins in a cell. So, the sequence of nitrogen
      bases should have a pattern in some important functional sections. These patterns can be revealed by
      machine learning methods such as <strong>convolutional neural networks</strong>.
    </p>
    <p>This is the result for the first 900 nucleotides:</p>
    <div class="boxes">
      <DnaBox v-for="(item, index) in piDigits" v-bind:key="index" v-bind:elem="item"/>
    </div>
    <br>
    <p>This is the result for the first 3600 nucleotides:</p>
    <div class="dna3600">
      <DnaBox v-for="(item, index) in dnaVals" v-bind:key="index" v-bind:elem="item"/>
    </div>
  </div>
</template>

<script>
import nucleotides from './utils/coronaDna.json';
import DnaBox from './DnaBox.vue';

export default {
  name: 'CoronaDna',
  components: {
    DnaBox
  },
  computed: {
    dnaVals: function() {
      return this.nucleotides.seq2.split('');
    },
    piDigits: function() {
      return this.nucleotides.seq.split('');
    }
  },
  data: function() {
    return {
      nucleotides: nucleotides,
      bases: ['a', 'c', 'g', 't']
    }
  },
  methods: {
    logPi: function() {
      //console.log(this.nucleotides.pi);
      console.log(this.piDigits);
    }
  }
  
}
</script>

<style scoped>
h1 {
  text-align: center;
}
.container {
  width: 70%;
  margin: auto;
}
.boxes {
  width: 70%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(30, 1fr);
  grid-template-rows: repeat(30, 20px);
}
.dna3600 {
  width: 70%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(60, 1fr);
  grid-template-rows: repeat(60, 10px);
}
.convention {
  display: grid;
  grid-template-columns: repeat(4, 25px);
  grid-template-rows: repeat(2, 20px);
  text-align: center;
  justify-content: center;
}
</style>